import base from './base'

const INTERVALS = {
                      '1':  0,
            'b2':  1, '2':  2, '#2': 3,
            'b3':  3, '3':  4,
            'b4':  4, '4':  5, '#4': 6,
            'b5':  6, '5':  7, '#5': 8,
            'b6':  8, '6':  9,
  'bb7': 9, 'b7': 10, '7': 11,
}

const Parent = base(INTERVALS)

export default class Interval extends Parent {
  static value(value) {
    return Parent.value(value, Interval)
  }
}
